import { Component, Prop, State, Event, EventEmitter, h, Host, Watch } from '@stencil/core';

export interface ChildItem {
  label: string;
  badge?: string | number;
  showBadge?: boolean;
  isActive?: boolean;
}

export interface NavigationItem {
  id: string;
  label: string;
  icon?: string;
  type: 'single' | 'expandable';
  isActive?: boolean;
  isExpanded?: boolean;
  badge?: string | number;
  children?: ChildItem[];
}

@Component({
  tag: 'bh-page-navigation',
  styleUrl: 'bh-page-navigation.css',
  shadow: true,
})
export class BhPageNavigation {
  /**
   * Title displayed at the top of the navigation
   */
  @Prop() navTitle: string = 'Navigation';

  /**
   * Array of navigation items
   */
  @Prop() navigationItems: NavigationItem[] = [];

  /**
   * Internal state to track navigation items
   */
  @State() items: NavigationItem[] = [];

  /**
   * Event emitted when a navigation item is clicked
   */
  @Event() bhItemClick!: EventEmitter<{ itemId: string; childIndex?: number }>;

  @Watch('navigationItems')
  handleNavigationItemsChange(newValue: NavigationItem[]) {
    this.items = [...newValue];
  }

  componentWillLoad() {
    this.items = [...this.navigationItems];
  }

  private clearAllSelections() {
    this.items = this.items.map(item => ({
      ...item,
      isActive: false,
      children: item.children?.map(child => ({ ...child, isActive: false })),
    }));
  }

  private handleSingleLevelClick = (itemId: string) => {
    this.clearAllSelections();
    this.items = this.items.map(item =>
      item.id === itemId ? { ...item, isActive: true } : item,
    );
    this.bhItemClick.emit({ itemId });
  };

  private handleMultiLevelToggle = (itemId: string) => {
    this.items = this.items.map(item =>
      item.id === itemId ? { ...item, isExpanded: !item.isExpanded } : item,
    );
  };

  private handleMultiLevelChildClick = (itemId: string, childIndex: number) => {
    this.clearAllSelections();
    this.items = this.items.map(item =>
      item.id === itemId && item.children
        ? {
            ...item,
            children: item.children.map((child, index) => ({
              ...child,
              isActive: index === childIndex,
            })),
          }
        : item,
    );
    this.bhItemClick.emit({ itemId, childIndex });
  };

  render() {
    return (
      <Host>
        <div class="page-navigation">
          <div class="page-navigation-header">
            <h2 class="page-navigation-title">{this.navTitle}</h2>
          </div>

          <nav class="page-navigation-list">
            {this.items.map(item =>
              item.type === 'single' ? (
                <bh-page-navigation-single-level
                  key={item.id}
                  label={item.label}
                  icon={item.icon}
                  isActive={item.isActive || false}
                  onBhItemClick={() => this.handleSingleLevelClick(item.id)}
                ></bh-page-navigation-single-level>
              ) : (
                <bh-page-navigation-multi-level
                  key={item.id}
                  label={item.label}
                  icon={item.icon}
                  childItems={item.children || []}
                  isExpanded={item.isExpanded || false}
                  onBhToggle={() => this.handleMultiLevelToggle(item.id)}
                  onBhChildClick={(e: CustomEvent<number>) =>
                    this.handleMultiLevelChildClick(item.id, e.detail)
                  }
                ></bh-page-navigation-multi-level>
              ),
            )}
          </nav>
        </div>
      </Host>
    );
  }
}
